import React, {PureComponent} from 'react';
import YTSearch from 'youtube-api-search';
// import youtubeAPI from './youtube-api.js';

export default class Youtube extends PureComponent{
    constructor(props){
        super(props);
        this.state = {
            term: 'rendom',
            videos: []
        }

        YTSearch({key:'AIzaSyBSyGpLXgYYPzOUXpOK0gQ9AkdIZbsvl2w',term: this.state.term},(videos)=>{
            this.setState({...this.state, videos });
            console.log(this.state.videos);
        });
    }

    searchLists = (key = 'rendom') => {
       
    }

    videoItem = () => {
        return this.state.videos.map(video => {
            console.log(video);
            return (
            <li key={video.etag}>
                {/* {video.id.videoId} */}
                {/*
                <video controls="true">
                    <source src={`https://www.youtube.com/watch?v=${video.id.videoId}`} />
                </video>
                */}
                <iframe width="420" height="315"
                    src={`https://www.youtube.com/embed/${video.id.videoId}`}>
                </iframe>
                {/* <img src={video.snippet.thumbnails.default.url} /> */}
                <span>{video.snippet.title}</span>
            </li>);
        })
    }

    videoViews = (code) => {
        switch (code) {
            case 'list':
                return (
                <div>
                    <ul>{this.videoItem()}</ul>
                </div>
                )
            case 'detail':
                break;
            default:
                break;
        }
    }

    render(){
        this.searchLists();
        return(
            <div>
                <div className="video-head">
                    <div className="v-head-inner">
                        <div className="hamburger-menu">

                        </div>
                        <h3><img scr="" alt="logo"/> </h3>
                        <div className="v-head-search">
                            <input 
                                type="text" 
                                value={this.state.term}
                                onChange={e => {this.setState({ term: e.target.value })}}
                            />
                            <button onClick={() => alert(this.state.term)}>검색</button>
                        </div>
                        <div>
                            계정
                        </div>
                    </div>
                </div>
                <div className="video-content">
                    {this.videoViews('list')}
                </div>
            </div>
        );
    }
}